$font-static: "PingHei",Helvetica,Arial,Verdana,sans-serif;

*{
  margin: 0;
  padding: 0;
}
html,body{
  height: 100%;
}
body{
  font-size: $font-static;
}
ul,ol,li{
  list-style: none;
}

.shareLink{
  padding-top: 25px;
  text-align: center;
  img{
    display: block;
  }
  .book-pic{
    margin: 0 auto;
  }
  p{
    text-align: center;
  }
  .book-tit{
    margin: 6px 10px;
    line-height: 24px;
    font-size: 16px;
    color: #333;
    span{
      color: #999;
      font-size: 14px;
    }
  }
  .book-dt{
    color: #666;
    font-size: 15px;
  }
  .toRead{
    margin: 18px auto;
    display: block;
    font-size: 16px;
    width: 130px;
    height: 35px;
    line-height: 35px;
    color: #fff;
    background-color: #10c1ae;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    border-radius: 6px;
  }
  .book-msg{
    padding: 0 18px;
    font-size: 16px;
    color: #666;
    line-height: 26px;
    text-align: left;
  }
  .linkToApp{
    position: fixed;
    bottom: 0;
    width: 100%;
    img{
      width: 100%;
    }
  }
}